// A set of css useful function helpers
const FreeStyle = require('free-style')

// the div element for injecting styles
let styles = document.getElementById('F-app-styles')

function createStylesContainer(target) {
  let styles = document.createElement('div')
  styles.id = 'F-app-styles'
  target.appendChild(styles)
  return styles
}

if (!styles) {
  styles = createStylesContainer(document.head)
}

function createModuleStylesContainer(name, scope = '') {
  let container = document.createElement('div')
  if (document.getElementById('fractalModuleStyles-' + scope + name)) {
    console.warn('WARNING!!! there are a duplicated module definition name!!: ' + scope + name)
  }
  container.id = scope + name
  styles.appendChild(container)
  return container
}

function createStyle(name, scope = '') {
  return {
    Style: FreeStyle.create(),
    container: createModuleStylesContainer(name, scope),
  }
}


function r(Style, styleObj) {
  let classHash = Style.registerStyle(styleObj)
  return classHash
}

function hasBaseObject(obj) {
  for (let key in obj) {
    if (obj[key] !== null && typeof obj[key] === 'object' && key == 'base') {
      return true
    }
  }
  return false
}

function rs(styleInstance, stylesObj) {
  function rs_func(stylesObj) {
    if (!hasBaseObject(stylesObj)) {
      return r(styleInstance.Style, stylesObj)
    }
    let classObj = {}
    for (let key in stylesObj) {
      if (hasBaseObject(stylesObj[key])) {
        classObj[key] = rs_func(stylesObj[key])
      } else if (stylesObj[key] != null && typeof stylesObj[key] === 'object') {
        classObj[key] = r(styleInstance.Style, stylesObj[key])
      } else { // function
        classObj[key] = stylesObj[key]
      }
    }
    return classObj
  }

  let classObj = rs_func(stylesObj)

  styleInstance.Style.inject(styleInstance.container)

  return classObj
}

let noSelectable = {
  '-webkit-touch-callout': 'none', /* iOS Safari */
  '-webkit-user-select': 'none',   /* Chrome/Safari/Opera */
  '-khtml-user-select': 'none',    /* Konqueror */
  '-moz-user-select': 'none',      /* Firefox */
  '-ms-user-select': 'none',       /* Internet Explorer/Edge */
  'user-select': 'none',
}

let absoluteCenter = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}

export default {
  r,
  rs,
  createStyle,
  helpers: {
    absoluteCenter,
    noSelectable,
  },
}



